
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import {httpUrl} from '../../http_url';

const list = () => [
{title:'登录',id:0},
{title:'注册',id:1}
]
export default {



data () {
return {
menus: {
menu1: '退出登录'
},
showMenus: false,

list1:['265001-315000','315001-365000','365001-415000','415001-465000','以上都不是'],
list2:['西藏自治区拉萨市','湖南省株洲市','香港特别行政区','重庆市','以上都不是'],
list3:['陕西省铜川市','湖南省株洲市','黑龙江省黑河市','山西省朔州市','以上都不是'],
list4:['西藏自治区拉萨市','湖南省株洲市','香港特别行政区','重庆市','以上都不是'],
list5:['丧偶','已婚','离婚','未婚','以上都不是'],



}
},
created(){

},
components: {
XHeader,
PopupHeader,
Actionsheet,
XButton,
Checklist
},
methods:{
getData(id){
console.log(id);
const _this = this;
let postData = this.$qs.stringify({
status:id
});
this.$axios({
method: 'post',
url:httpUrl.txorderList,
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.signeList = res.data.retData;
this.length = res.data.retData.length;
this.status_name = res.data.retData.status_name;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',



})
}
});
},
change(val,label){
console.log('change', val, label)

},
submit(){
this.$router.push('/cr_suc');
},

LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{

if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},



goHome(){
this.$router.push('/home');
}
}
}





